// SavingsWallet.js
import React from 'react';
import './SavingsWallet.css';

function SavingsWallet({ savings, wallet, useFromWallet, setUseFromWallet, onResetSavings, onResetWallet }) {
  return (
    <div className="savings-wallet-container">
      <div className="savings-section">
        <h2 className="section-title">  {savings.toFixed(2)} .</h2>
        
        <button className="reset-btn" onClick={onResetSavings}> </button>
      </div>
      <div className="wallet-section">
        <h2 className="section-title"> {wallet.toFixed(2)} .</h2>
       
        <button className="reset-btn" onClick={onResetWallet}> </button>
        {wallet > 0 && (
          <div className="use-wallet-input">
            <label className="input-label">     (.):</label>
            <input 
              type="number" 
              value={useFromWallet} 
              onChange={(e) => setUseFromWallet(e.target.value)} 
              placeholder="" 
              min="0" 
              step="0.01" 
              max={wallet} 
              className="wallet-input"
            />
          </div>
        )}
      </div>
    </div>
  );
}

export default SavingsWallet;
