import React from 'react';
import './Calculation.css';

function Calculation({ incomes, expenses, onCalculate, calculation, totalRemaining }) {
  const remainingClass = totalRemaining !== null ? (totalRemaining >= 0 ? 'positive' : 'negative') : '';

  return (
    <div className="calculation-container">
      <button 
        onClick={onCalculate} 
        disabled={expenses.length === 0} 
        className="calculate-btn"
        aria-label="   "
      >
        
      </button>
      {calculation && (
        <div className="result-section">
          <h2 className="result-title"></h2>
          <ul className="result-list">
            {calculation.map((item, idx) => <li key={idx} className="result-item">{item}</li>)}
          </ul>
        </div>
      )}
      {totalRemaining !== null && (
        <div className="remaining-section">
          <h2 className="remaining-title"></h2>
          <p className={`remaining-amount ${remainingClass}`}>  : {totalRemaining.toFixed(2)} .</p>
        </div>
      )}
    </div>
  );
}

export default Calculation;
