import React from 'react';
import './ExpenseList.css';  //   

function ExpenseList({ expenses, onDeleteExpense }) {
  return (
    <div className="expense-list">
      <h2 className="expense-list-title"> </h2>
      <ul className="expense-list-items">
        {expenses.map((exp, idx) => (
          <li key={idx} className={`expense-list-item ${exp.priority === '' ? 'priority-high' : exp.priority === '' ? 'priority-medium' : 'priority-low'}`}>
            <span className="expense-name">{exp.name}</span>
            <span className="expense-date">({exp.date})</span>
            <span className="expense-amount">{exp.amount.toFixed(2)} .</span>
            <span className="expense-priority">[: {exp.priority}]</span>
            <button 
              className="expense-delete-btn" 
              onClick={() => onDeleteExpense(idx)}
              aria-label={` : ${exp.name}   ${exp.amount.toFixed(2)} .`}
            >
              
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default ExpenseList;
