// IncomeForm.js
import React from 'react';
import './IncomeForm.css';

function IncomeForm({ incomeDate, incomeName, setIncomeDate, incomeAmount, setIncomeAmount, onAddIncome, setIncomeName }) {
  return (
    <div className="income-form-container">
      <h2 className="income-form-title"> (  )</h2>
      <form className="income-form" onSubmit={onAddIncome}>
        <label className="form-label"> :</label>
        <input 
          type="date" 
          value={incomeDate} 
          onChange={(e) => setIncomeDate(e.target.value)} 
          className="date-input"
          required 
        />
        <label className="form-label"> :</label>
        <input 
          type="string" 
          value={incomeName} 
           placeholder=" " 
          onChange={(e) => setIncomeName(e.target.value)} 
          className="amount-input"
          required 
        />
        <label className="form-label"> :</label>
        <input 
          type="number" 
          value={incomeAmount} 
          onChange={(e) => setIncomeAmount(e.target.value)} 
          placeholder=" " 
          min="0.01" 
          step="0.01" 
          className="amount-input"
          required 
        />
        <button type="submit" className="add-income-btn"> </button>
      </form>
      <p className="note">:      1000 . 10%  ,     , 90%   .</p>
    </div>
  );
}

export default IncomeForm;

